import { Dificultad, Ejercicio, NumeroSorteado } from "./Interfaces";

export function generarNuevoEjercicio(nivelDificultad: Dificultad = 5) {
	const ejercicio = obtenerEjercicio(nivelDificultad);
	return <Ejercicio> {
		nivelDificultad: nivelDificultad,
		numeroSorteado: ejercicio
	};

}

function obtenerEjercicio(dificultad: Dificultad) {
	const callback = {
		// 0: () => {

		// },
		1: () => {
			const numeroSorteado = Phaser.Math.Between(4, 12);
			const solucionNum1 = Phaser.Math.Between(1, numeroSorteado - 1);
			const listaNumeros: number[] = [solucionNum1, numeroSorteado - solucionNum1];
			do {
				const numAzar = Phaser.Math.Between(1, 7);
				if (!listaNumeros.includes(numAzar)) {
					listaNumeros.push(numAzar);
				}
			} while (listaNumeros.length < 4);
			return <NumeroSorteado> {
				numero: numeroSorteado,
				listaOpciones: listaNumeros
			};
		},
		2: () => {
			const numeroSorteado = Phaser.Math.Between(4, 15);
			const solucionNum1 = Phaser.Math.Between(1, numeroSorteado - 1);
			const listaNumeros: number[] = [solucionNum1, numeroSorteado - solucionNum1];
			do {
				const numAzar = Phaser.Math.Between(1, 10);
				if (!listaNumeros.includes(numAzar)) {
					listaNumeros.push(numAzar);
				}
			} while (listaNumeros.length < 4);
			return <NumeroSorteado> {
				numero: numeroSorteado,
				listaOpciones: listaNumeros
			};
		},
		3: () => {
			const numeroSorteado = Phaser.Math.Between(7, 19);
			const solucionNum1 = Phaser.Math.Between(1, numeroSorteado - 1);
			const listaNumeros: number[] = [solucionNum1, numeroSorteado - solucionNum1];
			do {
				const numAzar = Phaser.Math.Between(5, 14);
				if (!listaNumeros.includes(numAzar)) {
					listaNumeros.push(numAzar);
				}
			} while (listaNumeros.length < 6);
			return <NumeroSorteado> {
				numero: numeroSorteado,
				listaOpciones: listaNumeros
			};
		},
		4: () => {
			const numeroSorteado = Phaser.Math.Between(11, 24);
			const solucionNum1 = Phaser.Math.Between(8, 20);
			const listaNumeros: number[] = [solucionNum1, numeroSorteado - solucionNum1];
			do {
				const numAzar = Phaser.Math.Between(8, 20);
				if (!listaNumeros.includes(numAzar)) {
					listaNumeros.push(numAzar);
				}
			} while (listaNumeros.length < 6);
			return <NumeroSorteado> {
				numero: numeroSorteado,
				listaOpciones: listaNumeros
			};
		},
		5: () => {
			const numeroSorteado = Phaser.Math.Between(17, 29);
			const solucionNum1 = Phaser.Math.Between(11, 20);
			const listaNumeros: number[] = [solucionNum1, numeroSorteado - solucionNum1];
			do {
				const numAzar = Phaser.Math.Between(11, 20);
				if (!listaNumeros.includes(numAzar)) {
					listaNumeros.push(numAzar);
				}
			} while (listaNumeros.length < 6);
			return <NumeroSorteado> {
				numero: numeroSorteado,
				listaOpciones: listaNumeros
			};
		},

	};
	return <NumeroSorteado> callback[dificultad]();
}
