export class BarraTiempoComponent {

	private bar: Phaser.GameObjects.Graphics;
	private progreso: number;
	private textoFaltante: Phaser.GameObjects.Text;
	private eventoTiempo: Phaser.Time.TimerEvent;
	private barraHorizontal: boolean;
	private porcentajeRojo = 0.5;
	constructor (private escena: Phaser.Scene, private configuracion:
		{
			x: number,
			y: number,
			alto: number,
			ancho: number,
			totalSegundos: number,
			listaCallback?: {tiempo: number, callback: () => void}[],
		})
	{
		this.progreso = this.configuracion.totalSegundos;
		this.barraHorizontal = this.configuracion.ancho > this.configuracion.alto;
		this.bar = this.escena.add.graphics();
	}

	comenzar() {
		this.textoFaltante?.destroy();
		this.eventoTiempo?.destroy();
		this.textoFaltante = this.escena.add.text(this.configuracion.x + (this.configuracion.ancho / 2), this.configuracion.y + (this.configuracion.alto / 2),
			`${this.progreso}`, { fontFamily: "Gobold-Bold", fontSize: 24, fontStyle: 'bold', stroke: '#ffffff', strokeThickness: 7, color: '#000000' })
			.setOrigin(0.5, 0.5)
			.setAngle(this.barraHorizontal ? 0 : 90);
		this.pintar();
		this.progreso = this.configuracion.totalSegundos;
		this.eventoTiempo = this.escena.time.addEvent({
			delay: 1000,
			repeat: -1,
			callback: () => {
				this.progreso--;
				this.pintar();
				if (this.progreso === 0) {
					this.eventoTiempo.destroy();
				}
				this.configuracion.listaCallback?.find(c => c.tiempo === this.progreso)?.callback?.();
			}
		});
	}

	eliminar() {
		this.bar.clear();
		this.textoFaltante?.destroy();
		this.eventoTiempo?.destroy();
	}
	// avanzar(cantidad = 1) {
	// 	this.progreso += cantidad;
	// 	if (this.progreso > this.total) {
	// 		return;
	// 	}
	// 	this.pintar();
	// }

	// reiniciar() {
	// 	this.progreso = this.configuracion.totalSegundos;
	// 	this.pintar();
	// }

	// decrease (amount)
	// {
	// 	this.value -= amount;

	// 	if (this.value < 0)
	// 	{
	// 		this.value = 0;
	// 	}

	// 	this.draw();

	// 	return (this.value === 0);
	// }

	private pintar() {
		this.bar.clear();
		this.bar.fillStyle(0x000000);
		this.bar.fillRect(this.configuracion.x, this.configuracion.y, this.configuracion.ancho, this.configuracion.alto);

		this.bar.fillStyle(0xffffff);
		this.bar.fillRect(this.configuracion.x + 3, this.configuracion.y + 3, this.configuracion.ancho - 6, this.configuracion.alto - 6);

		if (this.progreso < this.configuracion.totalSegundos * this.porcentajeRojo) {
			this.bar.fillStyle(0xff0000);
		} else {
			this.bar.fillStyle(0x00ff00);
		}
		if (this.barraHorizontal) {
			// SIN PROBAR
			const d = Math.floor(this.progreso * ((this.configuracion.ancho - 6)/ this.configuracion.totalSegundos));
			this.bar.fillRect(this.configuracion.x + 3, this.configuracion.y + 3, d, this.configuracion.alto - 6);
		} else {
			// const d = Math.floor(this.progreso * ((this.configuracion.alto - 6)/ this.configuracion.totalSegundos));
			// this.bar.fillRect(this.configuracion.x + 3, this.configuracion.y + 3, this.configuracion.ancho - 6, d); <-- asi la barra verde desaparece desde abajo hacia arriba
			const d = Math.floor(this.progreso * ((this.configuracion.alto - 6) / this.configuracion.totalSegundos));
			this.bar.fillRect(this.configuracion.x + 3, this.configuracion.y + (this.configuracion.alto - 3) - d, this.configuracion.ancho - 6, d);
		}
		this.textoFaltante?.setText(`${this.progreso}`);
	}

}