	// 			return;
	// 		}
	// 		this.registry.events.emit(Eventos.PulsarTecla, event.keyCode);
	// 	});

	// 	this.registry.events.removeListener(Eventos.PulsarTecla);
	// 	this.registry.events.on(Eventos.PulsarTecla, (teclaPulsada: number) => {
	// 		if (this.ejercicioActual < this.listaEjercicios.length) {
	// 			if (teclaPulsada === Phaser.Input.Keyboard.KeyCodes.DELETE) {
	// 				this.ejercicioRespuestaActual = this.ejercicioRespuestaActual.slice(0, -1);
