export class BarraProgresoComponent {

	private bar: Phaser.GameObjects.Graphics;
	private progreso: number;
	private textoAvance: Phaser.GameObjects.Text;
	constructor (private escena: Phaser.Scene, private x: number, private y: number, private total: number)
	{
		this.bar = escena.add.graphics();
		this.progreso = 0;
		this.pintar();
	}

	avanzar(cantidad = 1) {
		this.progreso += cantidad;
		if (this.progreso > this.total) {
			return;
		}
		this.pintar();
	}

	reiniciar(total: number = null) {
		this.total = total ?? this.total;
		this.progreso = 0;
		this.pintar();
	}

	// decrease (amount)
	// {
	// 	this.value -= amount;

	// 	if (this.value < 0)
	// 	{
	// 		this.value = 0;
	// 	}

	// 	this.draw();

	// 	return (this.value === 0);
	// }

	private pintar() {
		this.textoAvance?.destroy();
		this.bar.clear();
		this.bar.fillStyle(0x000000);
		this.bar.fillRect(this.x, this.y, 1600, 80);

		this.bar.fillStyle(0xffffff);
		this.bar.fillRect(this.x + 4, this.y + 3, 1592, 74);

		// if (this.value < 30)
		// {
		// 	this.bar.fillStyle(0xff0000);
		// }
		// else
		// {
		// 	this.bar.fillStyle(0x00ff00);
		// }
		this.bar.fillStyle(0x00ff00);
		const d = Math.floor(this.progreso * (1600/ this.total));
		this.bar.fillRect(this.x + 2, this.y + 2, d, 76);
		this.textoAvance = this.escena.add.text(this.x + 800, this.y + 40, `${this.progreso}/${this.total}`, { fontFamily: "Gobold-Bold", fontSize: 64, fontStyle: 'bold', stroke: '#ffffff', strokeThickness: 7, color: '#000000' })
			.setOrigin(0.5, 0.5);
		// Phaser.Display.Align.In.Center(this.textoAvance, this.bar);
	}

}