export function esDispositivoiOS(){
	// const userAgent = window.navigator.userAgent;
	// return (/iP(hone|od|ad)/.test(userAgent));
	return !!navigator.platform.match(/iPhone|iPod|iPad/);

}

export function esDispositivoMovil() {
	return (!!navigator.userAgent.match(/Android/i) ||
			!!navigator.userAgent.match(/webOS/i) ||
			!!navigator.userAgent.match(/iPhone/i) ||
			!!navigator.userAgent.match(/iPad/i) ||
			!!navigator.userAgent.match(/iPod/i) ||
			!!navigator.userAgent.match(/BlackBerry/i) ||
			!!navigator.userAgent.match(/Windows Phone/i));
}

export function validarPulsacionTecla(tecla: number) {
	if (tecla >= Phaser.Input.Keyboard.KeyCodes.ZERO && tecla <= Phaser.Input.Keyboard.KeyCodes.NINE) {
		return true;
	}
	if (tecla >= Phaser.Input.Keyboard.KeyCodes.NUMPAD_ZERO && tecla <= Phaser.Input.Keyboard.KeyCodes.NUMPAD_NINE) {
		return true;
	}
	return [Phaser.Input.Keyboard.KeyCodes.DELETE, Phaser.Input.Keyboard.KeyCodes.BACKSPACE, Phaser.Input.Keyboard.KeyCodes.ENTER].includes(tecla);
}

export function transformarKeyCode(tecla: number) {
	const mapeo: {[keyCode: number] : number} = {
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_ONE]: Phaser.Input.Keyboard.KeyCodes.ONE,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_TWO]: Phaser.Input.Keyboard.KeyCodes.TWO,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_THREE]: Phaser.Input.Keyboard.KeyCodes.THREE,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_FOUR]: Phaser.Input.Keyboard.KeyCodes.FOUR,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_FIVE]: Phaser.Input.Keyboard.KeyCodes.FIVE,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_SIX]: Phaser.Input.Keyboard.KeyCodes.SIX,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_SEVEN]: Phaser.Input.Keyboard.KeyCodes.SEVEN,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_EIGHT]: Phaser.Input.Keyboard.KeyCodes.EIGHT,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_NINE]: Phaser.Input.Keyboard.KeyCodes.NINE,
		[Phaser.Input.Keyboard.KeyCodes.NUMPAD_ZERO]: Phaser.Input.Keyboard.KeyCodes.ZERO,
		[Phaser.Input.Keyboard.KeyCodes.BACKSPACE]: Phaser.Input.Keyboard.KeyCodes.DELETE,
	};
	return mapeo[tecla];
}

