	private crearEmitterFuego() {
		const rgbToHex = (r: number, g: number, b: number) => {
			const hexString = [r, g, b]
				.map((n) => {
					const hex = n.toString(16);
					return hex.length === 1 ? "0" + hex : hex;
				})
				.join("");
			return parseInt(hexString, 16);
		};
		const tintMin = rgbToHex(0, 0, 0);
		const tintMax = rgbToHex(255, 255, 255);

		this.particulasEmitterFuegoIzqDer = this.escena.add.particles(0, 1200, Imagenes.FuegoAmarillo, {
			// angle: { min: 135, max: 135 },
			// speed: { min: 200, max: 300 },
			// speedY: { min: 0, max: 300 },
			tint: {
				min: tintMin,
				max: tintMax
			},
			speedX: { min: 200, max: 300 },
			accelerationY: {min: -500, max: -200},
			lifespan: 8000,
			gravityY: 180,
			quantity: 1,
			frequency: 50,
			blendMode: 'ADD',
			emitZone: { type: 'edge', source: new Phaser.Geom.Rectangle(0, 0, 2048, 10), quantity: 70 },
			emitting: false,
			duration: 5000,
			// bounce: 0.75,
			// bounds: new Phaser.Geom.Rectangle(-100, -200, 1000, 750)
		});

		this.particulasEmitterFuegoDerIzq = this.escena.add.particles(0, 1200, Imagenes.FuegoAmarillo, {
			// angle: { min: 135, max: 135 },
			// speed: { min: 200, max: 300 },
			// speedY: { min: 0, max: 300 },
			tint: {
				min: tintMin,
				max: tintMax
			},
			speedX: { min: -200, max: -300 },
			accelerationY: {min: -500, max: -200},
			lifespan: 8000,
			gravityY: 180,
			// quantity: 1,
			frequency: 50,
			blendMode: 'ADD',
			emitZone: { type: 'edge', source: new Phaser.Geom.Rectangle(0, 0, 2048, 10), quantity: 70 },
			emitting: false,
			duration: 5000,
			// bounce: 0.75,
			// bounds: new Phaser.Geom.Rectangle(-100, -200, 1000, 750)
		});
		// const fx = emitter.postFX.addBokeh(0.5, 10, 0.2);
	}

