export interface DatosUsuario {
	balance: number;
}

export interface ParametrosEntrada {
	sessionToken: string;
	dificultad: string;
	modo: ModoJuego;
	tiempoPorPregunta: number;
	cantidadPreguntas: number;
	girar: boolean;
}

export interface MensajeParametros {
	titulo: string;
	detalle: string;
	callbackReintento: () => Promise<void>;
}

export interface Ejercicio {
	nivelDificultad: Dificultad;
	numeroSorteado: NumeroSorteado;
}

export interface NumeroSorteado {
	numero: number;
	listaOpciones: number[];

}

export enum ModoJuego {
	Aprender = 1, // Sin tiempo de preguntas (solo se cuentan las buenas)
	Ensayo = 2, // Ensayo (se cuentan las buenas y sin tiempo)
	Prueba = 3, // Prueba (Aca se cuentan las malas, no se permiten errores)
}

export enum Dificultad {
	Azar = 0,
	MuyFacil = 1, // 4 Incognitas
	Facil = 2, // 5 Incognitas
	Medio = 3, // 6 Incognitas
	Dificil = 4, // 7 Incognitas
	MuyDificil = 5, // 8 Incognitas
}