import { Imagenes } from "../Utilidades/Diccionario";

export class ContadorRespuestasComponent {
	private totalBuenas: number;
	private totalMalas: number;
	private totalSinContestar: number;
	private textoAvanceBuenas: Phaser.GameObjects.Text;
	private textoAvanceMalas: Phaser.GameObjects.Text;
	private textoAvanceSinContestar: Phaser.GameObjects.Text;
	private imagenAvanceBuenas: Phaser.GameObjects.Image;
	private imagenAvanceMalas: Phaser.GameObjects.Image;
	private imagenAvanceSinContestar: Phaser.GameObjects.Image;
	constructor (private escena: Phaser.Scene, private x: number, private y: number, private mostrarSinContestar: boolean, private mostrarMalas: boolean)
	{
		this.totalBuenas = 0;
		this.totalMalas = 0;
		this.totalSinContestar = 0;
		this.pintar();
	}

	agregarBuena() {
		this.totalBuenas++;
		this.textoAvanceBuenas.setText(`${this.totalBuenas}`);
	}

	agregarMala() {
		if (this.mostrarMalas) {
			this.totalMalas++;
			this.textoAvanceMalas.setText(`${this.totalMalas}`);
		}
	}

	agregarSinContestar() {
		if (this.mostrarSinContestar) {
			this.totalSinContestar++;
			this.textoAvanceSinContestar.setText(`${this.totalSinContestar}`);
		}
	}

	reset() {
		this.totalBuenas = 0;
		this.totalMalas = 0;
		this.totalSinContestar = 0;
		// this.textoAvanceBuenas.setText(`${this.totalBuenas}`);
		// this.textoAvanceMalas.setText(`${this.totalMalas}`);
		// this.textoAvanceSinContestar.setText(`${this.totalSinContestar}`);
		this.pintar();
	}

	private pintar() {
		this.textoAvanceBuenas?.destroy();
		this.textoAvanceMalas?.destroy();
		this.textoAvanceSinContestar?.destroy();
		this.imagenAvanceBuenas = this.escena.add.image(this.x, this.y + 50, Imagenes.ContadorBuenas).setDisplaySize(100, 100);
		this.textoAvanceBuenas = this.escena.add.text(this.x, this.y + 150, `${this.totalBuenas}`, { fontFamily: "Gobold-Bold", fontSize: 64, fontStyle: 'bold', stroke: '#ffffff', strokeThickness: 7, color: '#000000' })
			.setOrigin(0.5, 0.5);
		if (this.mostrarSinContestar) {
			this.imagenAvanceSinContestar = this.escena.add.image(this.x + 125, this.y + 50, Imagenes.ContadorTiempo).setDisplaySize(100, 100);
			this.textoAvanceSinContestar = this.escena.add.text(this.x + 125, this.y + 150, `${this.totalSinContestar}`, { fontFamily: "Gobold-Bold", fontSize: 64, fontStyle: 'bold', stroke: '#ffffff', strokeThickness: 7, color: '#000000' })
				.setOrigin(0.5, 0.5);
		}
		if (this.mostrarMalas) {
			this.imagenAvanceMalas = this.escena.add.image(this.x + 250, this.y + 50, Imagenes.ContadorMalas).setDisplaySize(100, 100);
			this.textoAvanceMalas = this.escena.add.text(this.x + 250, this.y + 150, `${this.totalMalas}`, { fontFamily: "Gobold-Bold", fontSize: 64, fontStyle: 'bold', stroke: '#ffffff', strokeThickness: 7, color: '#000000' })
				.setOrigin(0.5, 0.5);
		}

	}

}