	cambiarImagen(nuevoIndex: number) {
		if (this.imagenIndexMostrando === nuevoIndex) {
			return;
		}

		reproducirSonidoBotones(this);
		if (nuevoIndex > this.imagenesAyuda.length - 1) {
			nuevoIndex = 0;
		}

		if (nuevoIndex < 0) {
			nuevoIndex = this.imagenesAyuda.length - 1;
		}

		// const moverIzquierda = this.imagenIndexMostrando < nuevoIndex;
		this.imagenIndexMostrando = nuevoIndex;
		// for (const iterator of this.carruselCirculosSeleccionado) {
		// 	iterator.visible = false;
		// }
		// this.carruselCirculosSeleccionado[nuevoIndex].visible = true;

		this.tweens.add({
			targets: this.fotoActualAyuda,
			duration: 500,
			alpha: 0.1,
			// x: moverIzquierda ? -1000 : 1500,
			onStart: () => {
				// this.bola.y = !incrementar ? 200 : 400;
			},
			onComplete: () => {
				this.fotoActualAyuda.setTexture(this.imagenesAyuda[nuevoIndex]);
				this.tweens.add({
					targets: this.fotoActualAyuda,
					duration: 500,
					alpha: 1,
					// x: this.cameras.main.centerX,
					onStart: () => {
						// this.fotoActualAyuda.x = moverIzquierda ? 1500 : -1000;
						this.fotoActualAyuda.alpha = 0.1;
					},
					onComplete: () => {
						//
					}
				});
			}
		});
	}

	empezar() {
		this.globo.setVisible(true);
		this.contadorTexto.setText(this.contador.toString());
		const glowFX = this.globo.preFX.addGlow();
		glowFX.setActive(true);
		glowFX.outerStrength = 0;
		glowFX.innerStrength = 0;
		this.add.tween(
			{
				targets: glowFX,
				outerStrength: 16,
				innerStrength: 8,
				ease: 'sine.in',
				yoyo: true,
				duration: 500,
				repeat: 3
			});
		const emitter = this.add.particles(0, 0, Imagenes.FuegoAmarillo, {
			// frame: 'white',
			blendMode: 'ADD',
			lifespan: 1200,
			gravityY: -100,
			scale: { start: 0.3, end: 0 },
			emitting: false
		});
		emitter.addEmitZone({ source: this.globo.getBounds() });

		const timeline = this.add.timeline([
			{
				at: 0,
				run: () => {
					// console.log(new Date());
					this.crearEfectoCambioEscena();
					// glowFX.setActive(false);
					// glowFX.outerStrength = 0;
					// glowFX.innerStrength = 0;
					emitter.start();
				},
				tween: {
					targets: this.contadorTexto,
					scaleX: 1.5,
					scaleY: 1.5,
					duration: 500,
					yoyo: true,
				}
			},
			{
				at: 1000,
				run: () => {
					// console.log(new Date());
					this.contador--;
					this.contadorTexto.setText(this.contador.toString());
					// glowFX.setActive(true);
					// emitter.start();
				},
				tween: {
					targets: this.contadorTexto,
					scaleX: 1.5,
					scaleY: 1.5,
					duration: 500,
					yoyo: true,
				}
			},
			{
				at: 2000,
				run: () => {
					// console.log(new Date());
					this.contador--;
					this.contadorTexto.setText(this.contador.toString());
					// emitter.stop();
				},
				tween: {
					targets: this.contadorTexto,
					scaleX: 1.5,
					scaleY: 1.5,
					duration: 500,
					yoyo: true,
				}
			},
			{
				at: 3000,
				stop: true,
				run: () => {
					// console.log("termino");
					reproducirSonidoBotones(this);
					this.registry.set(Variables.EscenaActual, this.registry.get(Variables.EscenaLaunchMenu));
					this.scene.resume(this.registry.get(Variables.EscenaLaunchMenu));
					this.scene.stop(AyudaEscene.Name);
					this.registry.events.emit(Eventos.PulsarNuevoJuego);
				}
			}
		]);
		timeline.play();
		// this.tweens.add({
		// 	targets: this.contadorTexto,
		// 	scaleX: 1.5,
		// 	scaleY: 1.5,
		// 	duration: 400,
		// 	yoyo: true,
		// 	repeat: this.contador - 1,
		// 	onComplete: () => {
		// 		this.contador--;

		// 		if (this.contador=== 0) {
		// 			reproducirSonidoBotones(this);
		// 			this.registry.set(Variables.EscenaActual, this.registry.get(Variables.EscenaLaunchMenu));
		// 			this.scene.resume(this.registry.get(Variables.EscenaLaunchMenu));
		// 			this.scene.stop(AyudaEscene.Name);
		// 			this.registry.events.emit(Eventos.PulsarNuevoJuego);
		// 		} else {
		// 			this.empezar();
		// 		}
		// 	},
		// });
	}

	crearBotonempezar() {
		this.botonEmpezar = this.add.image(this.cameras.main.centerX, this.cameras.main.displayHeight - 220, AtlasImagenes.Botones, AtlasBotonesImagenes.BotonGrande)
			.setDepth(manejarDepthMainGame.profundidad3)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.empezar();
			})
			.on(Phaser.Input.Events.POINTER_MOVE, () => {
				this.botonEmpezar.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});

		this.textoBotonEmpezar = this.add.text(0, 0, "Comenzar", { fontFamily: "Gobold-Bold", fontSize: 48, fontStyle: 'bold', color: '#f7e13e', align: 'center' })
			.setOrigin(0.5, 0)
			.setDepth(manejarDepthMainGame.profundidad3);
		Phaser.Display.Align.In.Center(this.textoBotonEmpezar, this.botonEmpezar);

		this.add.particles(this.cameras.main.centerX, this.cameras.main.displayHeight - 220, Imagenes.FuegoAmarillo,
			{
				// frame: 'white',
				color: [ 0x96e0da, 0x937ef3 ],
				colorEase: 'quart.out',
				lifespan: 2500,
				angle: { min: -140, max: -40 },
				scale: { start: 1, end: 0, ease: 'sine.in' },
				speed: { min: 150, max: 200 },
				advance: 2000,
				frequency: 100,
				blendMode: 'ADD'
			});
	}

	crearEfectoCambioEscena() {
		const efectosCambioEscena = {
			0: () => {
				const fx = this.fondoImagen.preFX.addVignette(0.5, 0.5, 1, 0);
				this.tweens.add({
					targets: [ fx ],
					radius: 0.1,
					duration: 3000,
					yoyo: false,
					loop: 0,
					// hold: 1000,
					onUpdate: () => {
						// text.setText(`FX.Vignette.radius: ${fx.radius}`);
					},
					onComplete: () => {
						this.cameras.main.fadeOut(100);
						// this.scene.start('SceneA');
					}
				});
			},
			1: () => {
				const pixelated = this.cameras.main.postFX.addPixelate(-1);
				this.add.tween({
					targets: pixelated,
					delay: 2000,
					duration: 1000,
					amount: 40,
					onComplete: () => {
						this.cameras.main.fadeOut(100);
						// this.scene.start('SceneA');
					}
				});
			},
			2: () => {
				const fx = this.fondoImagen.preFX.addReveal(1, Phaser.Math.Between(0, 1), Phaser.Math.Between(0, 1));
				// const fx = this.fondoImagen.preFX.addReveal(1, 1, 1);
				fx.progress = 1;
				this.tweens.add({
					targets: fx,
					progress: 0,
					// repeatDelay: 1000,
					// hold: 1000,
					// yoyo: true,
					repeat: 0,
					// delay: 2000,
					duration: 3000
				});
			}
		}
		efectosCambioEscena[Phaser.Math.Between(0, 2)]();
	}
