import moment from "moment";

export class CronometroComponent {
	private inicio: Date;
	private eventoTiempo: Phaser.Time.TimerEvent;
	private texto: Phaser.GameObjects.Text;
	constructor(private escena: Phaser.Scene, private x, private y) {

	}

	iniciar() {
		this.texto?.destroy();
		this.texto = this.escena.add.text(this.x, this.y, "00:00", { fontFamily: "Gobold-Bold", fontSize: 64, fontStyle: 'bold', stroke: '#000000', strokeThickness: 7, color: '#ffffff' });
		this.inicio = new Date();
		this.eventoTiempo = this.escena.time.addEvent({
			delay: 1000,
			repeat: -1,
			callback: () => {
				this.actualizar();
			}
		});
	}

	detener() {
		this.eventoTiempo.destroy();
		this.actualizar();
		return moment().diff(this.inicio, 'seconds');
	}

	private actualizar() {
		// const ahora = new Date();
		// const segundos = (ahora.getTime() - this.inicio.getTime()) / 1000;
		// const minutos = Math.floor(segundos / 60);
		// const segundosRestantes = Math.floor(segundos % 60);
		// const texto = `${minutos}:${segundosRestantes < 10 ? "0" : ""}${segundosRestantes}`;
		// this.texto.setText(texto);
		const tiempo = moment.duration(moment().diff(moment(this.inicio)));
		if (tiempo.asHours() >= 1) {
			this.texto.setText(`${moment.utc(tiempo.as('milliseconds')).format('HH:mm:ss')}`);
		} else {
			this.texto.setText(`${moment.utc(tiempo.as('milliseconds')).format('mm:ss')}`);
		}
		// this.escena.registry.events.emit(Eventos.ActualizarTiempo, texto);
	}
}